import  Router  from 'express';
import { index, create } from './controllers/OngController';
import { index as _index, create as _create, delete as _delete } from './controllers/IncidentController';
import { index as __index } from './controllers/ProfileController';
import { create as __create } from './controllers/SessionController';
const routes = Router();

//login
routes.post('/sessions', __create);


//Profile
routes.get('/profile', __index);

//Ongs
routes.get('/ongs', index);
routes.post('/ongs', create);

//Incidents
routes.get('/incidents', _index);
routes.post('/incidents', _create);
routes.del('/incidents/:id', _delete);

export default routes;